.cd {
  -webkit-animation: cd-spin infinite 10s linear;
          animation: cd-spin infinite 10s linear;
  height: 80px;
  margin-right: 20px;
}
@-webkit-keyframes cd-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes cd-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



